.titleInfo-container {
  background-color: #101010;
  padding-top: 80px;
}
.containerLeft {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.left {
  display: flex;
  flex-direction: column;
  width: 600px;
  margin: 20px 0px;
}
.left-main-txt {
  color: #ffffff;
  font-size: 40px;
}
.main-text {
  background: -webkit-linear-gradient(#cadde3, #237faa);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 40px;
}
.info {
  color: #ffffff;
  font-size: 18px;
}
.logo {
  width: 500px;
  height: 400px;
}
.logo-view {
  margin-top: 60px;
}
.containerRight {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.title{
  margin-bottom: 30px;
}
/*tablet*/
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .left{
    width: 90%;
  }
  .logo {
    width: 500px;
  }
  .logo-view {
    margin-top: 0px;
  }
  .containerRight {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .left-main-txt{
    font-size: 30px;
  }
  .main-text{
    font-size: 35px;
  }
  .containerLeft {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
/*phone*/
@media screen and (max-width: 767px) {
  .left{
    width: 80%;
  }
  .logo {
    width: 300px;
    height: 250px;
  }
  .logo-view {
    margin-top: 0px;
  }
  .containerRight {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .left-main-txt{
    font-size: 30px;
  }
  .main-text{
    font-size: 35px;
  }
  .containerLeft {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
