.main-container {
  display: flex;
  justify-content: center;
}
.flip-card {
  perspective: 1000px;
  width: 345px;
  height: 430px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 1s;
  transform-style: preserve-3d;
  border-radius: 20px;
}

.flip-card.flipped .flip-card-inner {
  transform: rotateY(180deg);
}
.flip-card-front img {
  width: 100%;
  height: 200px;
  border-radius: 15px;
}
.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 15px;
}
.flip-card-front span,
.flip-card-back span {
  color: white;
}
.flip-card-front {
  background-color: #1e1d1d;
  color: #000;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.flip-card-back {
  background-color: #2a2a2a;
  color: #000;
  transform: rotateY(180deg);
}
.back-container{
  padding: 20px;
}
h2 {
  text-align: center;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  margin-top: 80px;
}
.card-title {
  font-size: 25px;
  font-weight: bolder;
  margin: 20px 0;
  padding: 0px 20px;
  display: flex;
}
.whole-body {
  background-color: #171717;
  /* margin: 80px 180px; */
  padding: 80px 180px;
}
.whole-body img{
  /* position: absolute; */
}
.title-span{
  display: flex;
  justify-content: center;
  background: -webkit-linear-gradient(#cadde3, #237faa);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 40px;
}
.read-more span{
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  color: #a3a3a3;
  cursor: pointer;
}
.second-span{
  display: flex;
  justify-content: center;
  color: #a3a3a3;
  font-size: 25px;
}
/* tablet */
@media screen and (min-width: 1024px) and (max-width: 1264px) {
  .grid-container {
    grid-template-columns: repeat(2, 1fr);
  }
} 
@media screen and (min-width: 768px) and (max-width: 1023px){
  .grid-container {
    grid-template-columns: repeat(2, 1fr);
  }
  .whole-body {
    padding: 80px 20px;
  }
  .flip-card {
    width: 325px;
    height: 375px;
  }
  .flip-card-front img {
    width: 100%;
    height: 190px;
  }
  .back-info{
    font-size: 15px;
  }
}

/* mobile */
@media screen and (max-width: 767px){
  .grid-container {
    grid-template-columns: repeat(1, 1fr);
  }
  .whole-body {
    padding: 80px 10px;
  }
  .flip-card {
    width: 250px;
    height: 325px;
  }
  .flip-card-front img {
    width: 100%;
    height: 150px;
  }
  .card-title {
    font-size: 20px;
    font-weight: bolder;
  }
  .title-span{
    font-size: 30px;
  }
  .second-span{
    font-size: 15px;
  }
  .back-info{
    font-size: 11px;
  }
}
