.team-main {
  background-color: rgb(31, 30, 30);
  padding-bottom: 80px;
}
.team-aboutus {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 80px 0;
}
.team-title {
  background: -webkit-linear-gradient(#cadde3, #237faa);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 40px;
}
.team-about {
  color: #a3a3a3;
  font-size: 20px;
}
.team-container {
  display: flex;
  justify-content: center;
  margin-bottom: 16px;
}
.Grid-container-1 {
  display: flex;
  justify-content: flex-end;
}
.Grid-container-2 {
  display: flex;
  justify-content: center;
}
.Grid-container-3 {
  display: flex;
  justify-content: flex-start;
}
.team-card {
  width: 340px;
  overflow: hidden;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
}
.card-image img {
  width: 100%;
  height: 140px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  object-fit: fill;
}
.profile-image img {
  object-fit: cover;
  z-index: 1;
  width: 150px;
  height: 150px;
  position: relative;
  margin-top: -90px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  border-radius: 100px;
  border: 5px solid white;
  transition-duration: 0.4s;
  transition-property: transform;
}
.profile-image img:hover {
  transform: scale(1.1);
}
.card-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.person-name {
  font-size: 22px;
  font-weight: 700;
}
.person-position {
  color: grey;
  font-size: 17px;
}
.icons {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}
.person-detail {
  padding: 20px;
}
.icons a {
  color: black;
}
.icons a:hover {
  color: rgb(38, 67, 129);
}
@media screen and (max-width: 767px) {
  .team-card {
    width: 260px;
  }
  .person-detail {
    font-size: 14px;
  }
  .team-title {
    font-size: 30px;
  }
  .team-about {
    font-size: 15px;
  }
}
