.Foot-container {
  display: flex;
  justify-content: center;
  background-color: #101010;
  color: white;
  padding: 30px 0px;
}
.logo-title img {
  width: 150px;
}
.foot-left {
  display: flex;
  justify-content: center;
}
.foot-right {
  display: flex;
  justify-content: center;
  align-items: center;
}
.contact-info {
  display: flex;
  flex-direction: column;
}
.Sicon {
  margin: 0px 10px;
}
.copyright {
  padding-bottom: 30px;
}
.text {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 15px;
}
hr {
  width: 95%;
  border-top: 1px solid gray;
  border-bottom: none;
  margin: 30px 0;
}
.hr-view {
  display: flex;
  justify-content: center;
}
.Company-name {
  background: -webkit-linear-gradient(#cadde3, #237faa);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 22px;
}
.social-media a{
  color: white;
}
.social-media a :hover{
  color: #237faa;
}
@media screen and (max-width: 767px) {
  .Foot-container {
    flex-direction: column;
  }
  .text span{
    font-size: 10px;
  }
}
