.trust-container{
    background-color: #171717;
    padding: 40px 0 80px 0;
}
.trust-title{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 40px 0;
}
.trust-title span{
  font-size: 40px;
  background: -webkit-linear-gradient(#cadde3, #237faa);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.trust-cards{
  display: flex;
  justify-content: center;
}
.trust-card{
  display: flex;
  justify-content: center;
}
.trust-img img{
  width: 200px;
  height: 100px;
  border-radius: 10px;
}
/*phone*/
@media screen and (max-width: 767px) {
  .trust-title span{
    font-size: 30px;
  }
}
/*tablet*/
@media screen and (min-width: 768px) and (max-width: 1023px) {
